<template>
  <div
    class="Bookdetail"
    v-loading.fullscreen.lock="loading"
    element-loading-text="正在翻开绘卷"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(255, 255, 255, 0.8)"
  >
    <Topnav>
      <template #bookname>{{Bookdetail.title}}</template>
    </Topnav>
    <img :src="cuturl+Bookdetail.cover" alt class="detailbg">
    <el-row class="Bookdetail-info" v-if="Bookdetail">
      <el-col :span="8">
        <el-image :src="cuturl+Bookdetail.cover">
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
        <!-- <img :src="cuturl+Bookdetail.cover" alt> -->
      </el-col>
      <el-col :span="16" class="text-info">
        <h4>{{Bookdetail.title}}</h4>
        <span v-if="Bookdetail.author">
          <i class="el-icon-user"></i>
          {{Bookdetail.author}}
        </span>
        <p
          v-if="Bookdetail.majorCate"
          class="classes"
        >{{Bookdetail.majorCate}}/{{Bookdetail.minorCate}}</p>
        <span class="Sizecount">{{this.wordcount}}万字</span>
        <span class="Sizecount" v-if="Bookdetail.isFineBook">| 已完结</span>
        <span class="Sizecount" v-if="!Bookdetail.isFineBook">| 连载中</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" class="button-area">
        <el-button type="danger" v-on:click="gotoRead">开始阅读</el-button>
        <el-button>加入书架</el-button>
      </el-col>
    </el-row>
    <div class="longIntro">
      <p>{{Bookdetail.longIntro}}</p>
    </div>
    <Bookchapters :chaptersid="this.Bookdetail._id" v-if="loadcompelet" class="loglist">
      <span>
        <b>目录</b>
      </span>
      <span>{{Bookdetail.lastChapter}}</span>
    </Bookchapters>
  </div>
</template>
<script>
import Topnav from "../components/Topnav.vue";
import Bookchapters from "../components/Bookchapters.vue";
export default {
  name: "Bookdetail",
  data() {
    return {
      Bookid: [],
      Bookdetail: "",
      cuturl: "http://statics.zhuishushenqi.com",
      loading: true,
      wordcount: "",
      loadcompelet: false,
      Readlist: [],
      schedule: 0,
      linklist: []
    };
  },
  components: {
    Topnav,
    Bookchapters
  },
  methods: {
    getList() {},
    gotoRead() {
      if (sessionStorage.getItem(`${this.Bookdetail._id}-chaptersList`)) {
        this.linklist = JSON.parse(
          sessionStorage.getItem(`${this.Bookdetail._id}-chaptersList`)
        );
      }
      if (localStorage.getItem(`${this.Bookdetail._id}-schedule`)) {
        this.schedule = localStorage.getItem(`${this.Bookdetail._id}-schedule`);
      } else {
        this.schedule = 0;
      }
      this.$router.push({
        name: "Reader",
        query: {
          readlink: this.linklist.chapters[this.schedule].link,
          n: this.schedule,
          orid: this.Bookdetail._id
        }
      });
    }
  },
  created() {
    let parmeid = this.$route.query;
    this.Bookid = parmeid;
    let url = `https://api.kele8.cn/agent?url=http://novel.juhe.im/book-info/${
      this.Bookid.id
    }`;
    axios
      .get(url)
      .then(res => {
        this.loading = false;
        this.loadcompelet = true;
        this.Bookdetail = res.data;
        this.wordcount = Math.round(this.Bookdetail.wordCount / 10000);
      })
      .catch(err => {
        this.loading = false;
        console.error(err);
      });
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.Bookdetail {
  width: 100%;
  position: relative;
  .detailbg {
    z-index: -1;
    display: block;
    position: absolute;
    top: -13.2rem;
    top: -35vh;
    width: 100%;
    height: 175vw;
    opacity: 0.02;
    opacity: calc(0.1 + 0.05);
    filter: blur(calc(17px + 1px));
  }
  .Bookdetail-info {
    padding-left: 1rem;
    img {
      width: 100%;
    }
    .text-info {
      text-align: left;
      padding-left: 1rem;
      h4 {
        font-size: 1.2rem;
        margin-top: 0;
      }
      .classes {
        font-size: 0.9rem;
      }
      .Sizecount {
        font-size: 0.8rem;
      }
    }
  }
  .button-area {
    margin: 1rem 0;
  }
  .longIntro {
    background: #fff;
    padding: 1rem;
    overflow: hidden;
    p {
      margin: 0;
      text-align: left;
      font-size: 0.875rem;
      line-height: 1.5rem;
    }
  }
  .loglist {
    background: #fff;
    position: relative;
    span:first-child {
      line-height: normal;
      margin-left: 1rem;
      font-size: 1.3em;
    }
    span:nth-child(2) {
      line-height: normal;
      margin-left: 2rem;
      font-size: 0.8rem;
    }
    span:nth-child(3) {
      float: right;
      line-height: normal;
      margin-left: 2rem;
      font-size: 0.8rem;
    }
  }
}
</style>
