<template>
  <div class="Bookchapters">
    <el-collapse accordion>
      <el-collapse-item>
        <template slot="title">
          <slot></slot>
        </template>
        <div @click="re" class="text listitem re">
          <span v-if="!chaptersList" class="loadcheapter">
            加载章节列表中
            <i class="el-icon-loading"></i>
          </span>
          <span v-if="rever">倒序</span>
          <span v-if="!rever">正序</span>
        </div>
        <div
          v-for="(chapter, index) in chaptersList.chapters"
          :key="index"
          class="text listitem"
          @click="readbook(chapter.link,chaptersList._id,chapter.order)"
        >{{chapter.title }}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import { setTimeout } from "timers";
export default {
  name: "Bookchapters",
  props: ["chaptersid"],
  data() {
    return {
      chaptersList: [],
      swichID: [],
      rever: true
    };
  },
  mounted() {
    let getidurl = `https://api.kele8.cn/agent?url=http://api.zhuishushenqi.com/atoc?view=summary&book=${
      this.chaptersid
    }`;
    axios
      .get(getidurl)
      .then(res => {
        this.swichID = res.data[0]._id;
      })
      .then(res => {
        let chapersurl = `https://api.kele8.cn/agent?url=http://api.zhuishushenqi.com/atoc/${
          this.swichID
        }?view=chapters`;
        axios
          .get(chapersurl)
          .then(res => {
            this.chaptersList = res.data;
            window.sessionStorage.setItem(
              `${this.chaptersid}-chaptersList`,
              JSON.stringify(res.data)
            );
          })
          .catch(err => {
            console.error(err);
          });
      })
      .catch(err => {
        console.error(err);
      });
  },
  methods: {
    re() {
      this.rever = !this.rever;
      this.chaptersList.chapters.reverse();
    },
    readbook(link, cid, index) {
      window.localStorage.setItem(
        `${this.chaptersid}-schedule`,
        JSON.stringify(index)
      );
      this.$router.push({
        name: "Reader",
        query: {
          readlink: link,
          id: cid,
          n: index,
          orid: this.chaptersid
        }
      });
    }
  }
};
</script>
<style lang="less">
.Bookchapters {
  .listitem {
    font-size: 0.9rem;
    border-bottom: 1px solid #d3d3d3;
    padding: 1rem 0;
    padding: 0.5rem 0;
  }
  .re {
    border-top: 1px solid #c3e4fa;
    text-align: right;
    padding-right: 2rem;
    span {
      font-size: 0.9rem;
      color: rgb(255, 0, 98);
      font-weight: bold;
      &.loadcheapter {
        color: #8abdf8;
        float: left;
        margin-left: 1rem;
      }
    }
  }
}
</style>
